import { Component, OnInit } from '@angular/core';
import {CartServiceService} from '../../services/order/cart-service.service';
import {ProductService} from '../../services/products/product.service';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.view.html',
  styleUrls: ['./cart.view.scss']
})
export class CartView implements OnInit {
  private cart = [];

  constructor(public cartService: CartServiceService, public  productService: ProductService) { }

  ngOnInit(): void {
  }

  ionViewDidEnter() {
    this.loadItemsOfCart();
  }

  cartItemEvent(event) {
    if (event.operation === 'delete'){
      this.loadItemsOfCart();
    }
  }

  loadItemsOfCart(){
    this.cart = Array.prototype.concat.apply([], this.cartService.getAllProductOrders());;
  }

  checkout() {
    this.cartService.checkoutCart();
  }
}
