import {Component, Input, OnInit, Output, EventEmitter} from '@angular/core';
import {Product} from '../../models/product/product';
import {CartServiceService} from '../../services/order/cart-service.service';
import {ProductOrder} from '../../models/cart/product-order';

@Component({
  selector: 'app-cart-item',
  templateUrl: './cart-item.component.html',
  styleUrls: ['./cart-item.component.scss']
})
export class CartItemComponent implements OnInit {

  @Input() product: ProductOrder;
  @Output() productEvent = new EventEmitter<any>();

  order: ProductOrder;
  orderIndex: number;

  constructor(public cartService: CartServiceService) { }

  ngOnInit(): void {
  }

  removeFromCart() {
    this.cartService.removeProductOrderFromCart(this.product);
    this.productEvent.emit({operation: 'delete', product: this.product}) ;
  }
}
