import {Component, OnDestroy, OnInit} from '@angular/core';
import {AngularFirestore} from '@angular/fire/firestore';
import {AngularFireStorage} from '@angular/fire/storage';
import {ActivatedRoute, Router} from '@angular/router';
import {Product} from '../../models/product/product';
import {User} from '../../models/user/user.model';
import {UserRoles} from '../../models/user/user-roles.enum';
import {ProfilesService} from '../../services/profiles/profiles.service';

@Component({
  selector: 'app-profile-list',
  templateUrl: './profile-list.view.html',
  styleUrls: ['./profile-list.view.scss']
})
export class ProfileListView implements OnInit {
  public profileList: User[];
  private profileImages: any[];
  private currentProfile: User;

  constructor(public firestore: AngularFirestore, public storage: AngularFireStorage,
              private router: Router, private route: ActivatedRoute, public profileService: ProfilesService) {
  }

  ionViewWillEnter() {
    this.profileList = [];
    this.profileImages = [];
    this.currentProfile = new User();

    this.profileService.getAllFarmers().then(value => this.profileList = value);
  }

  ngOnInit(): void {
  }

  async loadCurrentPicture(imageUrl: string, index: number) {
    const ref = this.storage.ref(imageUrl);
    ref.getDownloadURL().subscribe(url => {
      this.profileList[index].picture = url;
      return url;
    });
  }
}
