import {classToPlain, plainToClass} from 'class-transformer';

export class Product {
  private _id?: string;
  private _name: string;
  private _description: string;
  private _price: number;
  private _picture?: string;
  private _unit: UnitTypeEnum;
  private _color?: string;
  private _stock: boolean;
  private _ownerId: string;
  private _category?: CategoryEnum;

  constructor() {
    this._name = '';
    this._description = '';
    this._price = -1;
    this._unit = UnitTypeEnum.Kilogramo;
    this._stock = true;
  }

  get id(): string {
    return this._id;
  }

  set id(value: string) {
    this._id = value;
  }

  get name(): string {
    return this._name;
  }

  set name(value: string) {
    this._name = value;
  }

  get description(): string {
    return this._description;
  }

  set description(value: string) {
    this._description = value;
  }

  get price(): number {
    return this._price;
  }

  set price(value: number) {
    this._price = value;
  }

  get picture(): string {
    return this._picture;
  }

  set picture(value: string) {
    this._picture = value;
  }

  get unit(): UnitTypeEnum {
    return this._unit;
  }

  set unit(value: UnitTypeEnum) {
    this._unit = value;
  }

  get color(): string {
    return this._color;
  }

  set color(value: string) {
    this._color = value;
  }

  get stock(): boolean {
    return this._stock;
  }

  set stock(value: boolean) {
    this._stock = value;
  }

  get ownerId(): string {
    return this._ownerId;
  }

  set ownerId(value: string) {
    this._ownerId = value;
  }

  get category(): CategoryEnum {
    return this._category;
  }

  set category(value: CategoryEnum) {
    this._category = value;
  }

  convertToDTO() {
    return classToPlain(this);
  }

  convertFromDTO(product) {
    return plainToClass(Product, product);
  }
}

export enum UnitTypeEnum {
  Manojo = 'Manojo',
  Kilogramo = 'Kilogramo',
  Unidad = 'Unidad'
}

export enum CategoryEnum {
  Fruta = 'Fruta',
  Verdura = ' Verdura',
  Legumbres = 'Legumbres',
  Panaderia = 'Panaderia',
  Lacteos = 'Lacteos',
  Huevos = 'Huevos',
  Miel = 'Miel',
  Otros = 'Otros'
}
