import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {AngularFirestore, AngularFirestoreDocument} from '@angular/fire/firestore';
import {AngularFireAuth} from '@angular/fire/auth';
import {AngularFireStorage} from '@angular/fire/storage';
import {Product} from '../../models/product/product';
import {User} from '../../models/user/user.model';
import {ProductOrder} from '../../models/cart/product-order';
import {CartServiceService} from '../../services/order/cart-service.service';
import {ProductService} from '../../services/products/product.service';

@Component({
  selector: 'app-product',
  templateUrl: './product.view.html',
  styleUrls: ['./product.view.scss']
})
export class ProductView implements OnInit {
  public product: Product;
  private pathId: string;
  private productCollection: AngularFirestoreDocument<Product>;
  public productImage: any;
  isOwner = false;
  private loggedId: string;
  selectVal = 0;
  private productOrder: ProductOrder;

  constructor(private router: Router, private route: ActivatedRoute,
              public firestore: AngularFirestore, public afAuth: AngularFireAuth,
              public storage: AngularFireStorage, public cartService: CartServiceService,
              public productService: ProductService) {
    this.product = new Product();
    this.productOrder = new ProductOrder();
  }

  ionViewWillEnter(){
    this.route.params.subscribe( params => {
      if (params.id) {
        if (this.pathId !== params.id) {
          this.pathId = params.id;
          this.productService.loadProductById(this.pathId).then(value => {
            this.product = value;
            this.productOrder.product = this.product;
            this.productOrder.priceByEach = this.product.price;
            this.productOrder.productId = this.product.id;
            this.productOrder.productOwnerId = this.product.ownerId;

            this.afAuth.authState.subscribe(loggedUser => {
              if (loggedUser) {
                this.loggedId = loggedUser.uid;
                this.loggedId === this.product.ownerId ? this.isOwner = true : this.isOwner = false;
              }
            });
          });
        }
      }
    });
  }

  ngOnInit(): void {
  }

  addToCart() {
    // TODO: Quitar el boton de agregar y poner uno de eliminar del carrito
    this.cartService.addProductToCart(this.productOrder);
  }
}
