import {ProductOrder} from '../cart/product-order';
import {classToPlain, plainToClass} from 'class-transformer';

export class Order {
  private _id: string;
  private _userId: string;
  private _ownerId: string;
  private _creationDate: Date;
  private _status: OrderStatusEnum;
  private _orderedItems: ProductOrder[];

  constructor() {
    this._userId = 'TODO';
    this._creationDate = new Date();
    this._status = OrderStatusEnum.Pendiente;
    this._orderedItems = [];
  }

  get id(): string {
    return this._id;
  }

  set id(value: string) {
    this._id = value;
  }

  get userId(): string {
    return this._userId;
  }

  set userId(value: string) {
    this._userId = value;
  }

  get ownerId(): string {
    return this._ownerId;
  }

  set ownerId(value: string) {
    this._ownerId = value;
  }

  get creationDate(): Date {
    return this._creationDate;
  }

  set creationDate(value: Date) {
    this._creationDate = value;
  }

  get status(): OrderStatusEnum {
    return this._status;
  }

  set status(value: OrderStatusEnum) {
    this._status = value;
  }

  get orderedItems(): ProductOrder[] {
    return this._orderedItems;
  }

  set orderedItems(value: ProductOrder[]) {
    this._orderedItems = value;
  }

  convertToDTO() {
    return classToPlain(this);
  }

  convertFromDTO(order) {
    const productOrder = new ProductOrder();
    const newOrder = plainToClass(Order, order);
    newOrder.orderedItems.forEach((value, index) => {
      newOrder.orderedItems[index] = productOrder.convertFromDTO(value);
    });
    return plainToClass(Order, newOrder);
  }

  getOrderPrice() {
    let finalPrice = 0;
    this.orderedItems.forEach(value => {
      finalPrice += value.priceByEach * value.productAmount;
    });
    return finalPrice;
  }
}

export enum OrderStatusEnum {
  Pendiente =  'Pendiente',
  Preparado = 'Preparado',
  Entregado = 'Entregado',
  Cancelado = 'Cancelado'
}
