import {formatDate} from '@angular/common';
import {Product} from '../product/product';
import {classToPlain, plainToClass} from 'class-transformer';
import {UserRoles} from './user-roles.enum';

export class User {
  private _id?: string;
  private _email: string;
  private _phoneNumber?: string;
  private _name: string;
  private _picture?: string;
  private _location: string;
  private _balance: number;
  private _lastSession: number;
  private _role: UserRoles;

  constructor() {
    this.lastSession = Date.now();
    this.email = '';
    this.name = '';
    this.picture = '';
    this.location = '';
    this.balance = 0;
  }

  getCurrentDate(): string{
    return formatDate(this.lastSession, 'yyyy-MM-dd', 'es-ES');
  }

  get id(): string {
    return this._id;
  }

  set id(value: string) {
    this._id = value;
  }

  get email(): string {
    return this._email;
  }

  set email(value: string) {
    this._email = value;
  }

  get phoneNumber(): string {
    return this._phoneNumber;
  }

  set phoneNumber(value: string) {
    this._phoneNumber = value;
  }

  get name(): string {
    return this._name;
  }

  set name(value: string) {
    this._name = value;
  }

  get picture(): string {
    return this._picture;
  }

  set picture(value: string) {
    this._picture = value;
  }

  get location(): string {
    return this._location;
  }

  set location(value: string) {
    this._location = value;
  }

  get balance(): number {
    return this._balance;
  }

  set balance(value: number) {
    this._balance = value;
  }

  get role(): UserRoles {
    return this._role;
  }

  set role(value: UserRoles) {
    this._role = value;
  }

  get lastSession(): number {
    return this._lastSession;
  }

  set lastSession(value: number) {
    this._lastSession = value;
  }

  convertToDTO() {
    return classToPlain(this);
  }

  convertFromDTO(userData) {
    return plainToClass(User, userData);
  }
}
