import { Component, OnInit } from '@angular/core';
import {AngularFirestore, AngularFirestoreCollection} from '@angular/fire/firestore';
import {Product} from '../../models/product/product';
import {AngularFireStorage} from '@angular/fire/storage';
import {ActivatedRoute, Router} from '@angular/router';
import {ProductService} from '../../services/products/product.service';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.view.html',
  styleUrls: ['./product-list.view.scss']
})
export class ProductListView implements OnInit {
  public productList: Product[];
  private productImages: any[];
  private currentProduct: Product;
  private pathId: string;

  constructor(public firestore: AngularFirestore, public storage: AngularFireStorage,
              private router: Router, private route: ActivatedRoute, public productService: ProductService) {
    this.productList = [];
    this.productImages = [];
    this.currentProduct = new Product();

    this.route.params.subscribe( params => {
      if (params.userId) {
        this.pathId = params.userId;
        this.productList = [];
        this.productService.loadProductsByOwnerId(this.pathId).then(value => this.productList = value);
      } else {
        this.productService.loadAllProducts().then(value => this.productList = value);
      }
    });
  }

  ngOnInit(): void {
  }
}
