import { Injectable } from '@angular/core';
import {User} from '../../models/user/user.model';
import {UserRoles} from '../../models/user/user-roles.enum';
import {AngularFireAuth} from '@angular/fire/auth';
import {Router} from '@angular/router';
import {AngularFirestore, AngularFirestoreCollection} from '@angular/fire/firestore';
import {AngularFireStorage} from '@angular/fire/storage';

@Injectable({
  providedIn: 'root'
})
export class ProfilesService {
  private userCollection: AngularFirestoreCollection<unknown>;

  constructor(public afAuth: AngularFireAuth, public router: Router, public firestore: AngularFirestore,
              public storage: AngularFireStorage) {
    this.userCollection = this.firestore.collection('profiles');
  }

  loadUserById(userId: string){
    this.userCollection.doc(userId);
    return new Promise<any>((resolve, reject) => {
      this.userCollection.doc(userId).valueChanges({idField: '_id'}).subscribe(userData => {
        const realUser = new User().convertFromDTO(userData);
        resolve(realUser);
      });
    });
  }

  getAllFarmers(){
    const farmerCollection = this.firestore.collection('profiles', ref => ref.where('_role', '==', UserRoles.PRODUCER));
    let farmerList = [];
    return new Promise<any>((resolve, reject) => {
      farmerCollection.valueChanges({idField: '_id'}).subscribe(farmerEntities => {
        farmerList = [];
        farmerEntities.forEach(currentUser => {
          const realUser = new User().convertFromDTO(currentUser);
          this.loadProfileImage(realUser.picture).then(imageUrl => realUser.picture = imageUrl);
          farmerList.push(realUser);
        });
        resolve(farmerList);
      });
    });
  }

  async loadProfileImage(imagePath: string){
    const ref = this.storage.ref(imagePath);
    return new Promise<any>((resolve, reject) => {
      ref.getDownloadURL().subscribe(url => {
        resolve(url);
      });
    });
  }
}
