import { Injectable } from '@angular/core';
import {AngularFireAuth} from '@angular/fire/auth';
import {AngularFirestore, AngularFirestoreCollection} from '@angular/fire/firestore';
import {AngularFireStorage} from '@angular/fire/storage';
import {ProductOrder} from '../../models/cart/product-order';
import {Cart} from '../../models/cart/cart';
import {Order} from '../../models/order/order';
import {OrderItem} from '../../models/order/order-item';
import {ProductService} from '../products/product.service';
import {classToPlain} from 'class-transformer';
import {Product} from '../../models/product/product';
import {valueReferenceToExpression} from '@angular/compiler-cli/src/ngtsc/annotations/src/util';

@Injectable({
  providedIn: 'root'
})
export class OrderService {
  private orderCollection: AngularFirestoreCollection<unknown>;

  constructor(public afAuth: AngularFireAuth, public firestore: AngularFirestore, public storage: AngularFireStorage,
              public productService: ProductService) {
    this.orderCollection = this.firestore.collection('orders');
  }

  createOrderEntity(order: Order): any {
    const orderEntity = classToPlain(order);
    console.log(orderEntity);
    orderEntity._orderedItems.forEach(product => {
      delete product._product;
    });
    return orderEntity;
  }

  loadAllOrders(){
    let orderList: Order[] = [];
    return new Promise<any>((resolve, reject) => {
      this.orderCollection.valueChanges({idField: '_id'}).subscribe(orderEntities => {
        orderList = [];
        orderEntities.forEach((orderEntity) => {
          const orderModel = new Order().convertFromDTO(orderEntity);
          orderModel.orderedItems.forEach((product, index) => {
            this.productService.loadProductById(product.productId).then(value => {
              orderModel.orderedItems[index].product = value;
            });
          });
          orderList.push(orderModel);
        });
        resolve(orderList);
      });
    });
  }

  async loadProductImage(imagePath: string){
    const ref = this.storage.ref(imagePath);
    return new Promise<any>((resolve, reject) => {
      ref.getDownloadURL().subscribe(url => {
        resolve(url);
      });
    });
  }
}
