import {OrderItem} from '../order/order-item';
import {Product} from '../product/product';
import {classToPlain, plainToClass} from 'class-transformer';

export class ProductOrder {
  private _productId: string;
  private _productOwnerId: string;
  private _productAmount: number;
  private _priceByEach: number;
  private _product: Product;

  constructor() {
    this._productId = '-1';
    this._productAmount = 0;
    this._priceByEach = 0;
    this._productOwnerId = '-1';
  }

  reduce() {
    if (this.productAmount > 0){
      this.productAmount--;
    } else {
      this.productAmount = 0;
    }
  }

  add() {
    this.productAmount++;
  }

  convertToDTO() {
    return classToPlain(this);
  }

  convertFromDTO(product) {
    return plainToClass(ProductOrder, product);
  }

  get productAmount(): number {
    return this._productAmount;
  }

  set productAmount(value: number) {
    if (value > 0){
      this._productAmount = value;
    } else {
      this._productAmount = 0;
    }
  }

  get productId(): string {
    return this._productId;
  }

  set productId(value: string) {
    this._productId = value;
  }

  get priceByEach(): number {
    return this._priceByEach;
  }

  set priceByEach(value: number) {
    this._priceByEach = value;
  }

  get productOwnerId(): string {
    return this._productOwnerId;
  }

  set productOwnerId(value: string) {
    this._productOwnerId = value;
  }

  get product(): Product {
    return this._product;
  }

  set product(value: Product) {
    this._product = value;
  }
}
