import { Component, OnInit } from '@angular/core';
import {AngularFireAuth} from '@angular/fire/auth';
import {Router} from '@angular/router';
import {AngularFirestore, AngularFirestoreDocument} from '@angular/fire/firestore';
import {User} from '../../models/user/user.model';
import {UserRoles} from '../../models/user/user-roles.enum';
import {ProfilesService} from '../../services/profiles/profiles.service';

@Component({
  selector: 'app-navigation-bar',
  templateUrl: './navigation-bar.component.html',
  styleUrls: ['./navigation-bar.component.scss'],
})
export class NavigationBarComponent implements OnInit {
  public isFarmer = false;
  public isLogged = false;
  private userCollection: AngularFirestoreDocument;
  public userId: string;

  constructor(public afAuth: AngularFireAuth, public router: Router,
              public firestore: AngularFirestore,
              public profilesService: ProfilesService) {
    this.afAuth.authState.subscribe(user => {
      if (user) {
        this.isLogged = true;
        this.userId = user.uid;
        this.userCollection = firestore.doc('profiles/' + user.uid);
        this.profilesService.loadUserById(user.uid).then(realUser => {
          this.isFarmer = realUser.role === UserRoles.PRODUCER;
        });
      } else {
        this.isLogged = false;
      }
    });
  }

  ngOnInit() {
  }

}
